import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {CVService} from "../cv/cv.service";
import {MdDialog} from "@angular/material";
import {Amount} from "./amount.model";


@Component({
  selector: 'amount',
  templateUrl: './amount.html',
  providers: [CVService]

})
export class AmountComponent implements OnInit {

  @Input()
  amount: any;

  @Output() onSaved = new EventEmitter<Amount>();

/*  @Input()
  parent: any;*/

  amountType: any[] = [];
  amountUnits: any[] = [];
  amountDisplay: boolean = true;


  constructor(private cvService: CVService) {
      this.getAmountType();
      this.getAmountUnits();
  }

  getAmountType(): void {
    console.log("amount types.................");
    this.cvService
      .getList("AMOUNT_TYPE")
      .then(at => this.amountType = at);
  }

  getAmountUnits(): void {
    console.log("amount units.................");
    this.cvService
      .getList("AMOUNT_UNIT")
      .then(units => this.amountUnits = units);
  }

  ngOnInit(): void {
    if(!this.amount){
      this.amount = new Amount();
    }
  }

  clearAmount(): void {
    this.amount = new Amount();
    this.onSaved.emit(this.amount);
  }

  saveAmount(amount: Amount): void {
    this.onSaved.emit(amount);
  }

}



