import {Data} from "../data";
import {Reference} from "./reference";
import * as _ from "lodash";

export class ReferencedData extends Data<ReferencedData>{
  references: string[];

  hasReference(ref: Reference): Boolean {
    return this.references.indexOf(ref.uuid)>-1 ;
  }
  
  removeReference(ref: Reference): ReferencedData{
    var loc=this.references.indexOf(ref.uuid);
    if(loc>-1){
        this.references.splice(loc,1);
        ref.removeData(this);
    }
    
    return this;
  }
  
  addReference(ref: Reference): ReferencedData{
    var loc=this.references.indexOf(ref.uuid);
    if(loc==-1){
        this.references.push(ref.uuid);
        ref.addData(this);
    }
    return this;
  }
}



