import {ReferencedData} from "../references/referencedData";
import {PropertyService} from "./property.service"
import {Amount} from "../amount/amount.model";
import {Parameter} from "../parameters/parameter";

export class Property extends ReferencedData{

  constructor(private propertyService: PropertyService) {
    super();
  }

  relatedSubstance: any;
  references: any[];
  propertyType: any[];
  propertyName: any[];
  access: any[];
  value: Amount;
  parameters: Parameter[];
}



