import { Injectable }             from '@angular/core';
import {Relationship} from "../relationships/relationship";
import {Code} from "../codes/code";
import {Reference} from "../references/reference";
import {Name} from "../names/name";
import * as _ from "lodash";
import {Data} from "../data";


@Injectable()
export class SubstanceEditService {



  static uuid: string;
  static names: Name[] = null;
  public static refs: Reference[] = null;
  static codes: Code[] = null;
  static relations: Relationship[] = null;
  static substance: any = "";
  load:string = "done";


  setUuid(uuid: string){
    SubstanceEditService.uuid = uuid;
  }

  getNames(): Promise<Name[]> {
    if(SubstanceEditService.names !== null) return Promise.resolve(SubstanceEditService.names);

    this.load = "loading";
    return new Promise((resolve, reject) => {
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.fetch("names"))
        .andThen(names=> this.storeNames(names))
        .get(names=>resolve(names));

      this.load = "done";
    });

  }


  getReferences(): Promise<Reference[]> {
    if(SubstanceEditService.refs !== null) return Promise.resolve(SubstanceEditService.refs);

        return new Promise((resolve, reject) => {
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.fetch("references!sort(lastEdited)"))
        .andThen(refs=>this.storeReferences(refs))
        .get(refs=>resolve(refs));
    });
  }


  getCodes():Promise<Code[]> {
    if(SubstanceEditService.codes !== null) return Promise.resolve(SubstanceEditService.codes);

    return new Promise((resolve, reject) => {
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.fetch("codes"))
        .andThen(codes=> this.storeCodes(codes))
        .get(codes=>resolve(codes));
    });
  }


  getRelationships(): Promise<Relationship[]> {
    if(SubstanceEditService.relations !== null) return Promise.resolve(SubstanceEditService.relations);

    return new Promise((resolve, reject) => {
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.fetch("relationships"))
        .andThen(rels=> this.storeRelationships(rels))
        .get(rels=>resolve(rels));
    });
  }


  _getFullJson(): Promise<any> {

    if(SubstanceEditService.substance) return Promise.resolve(SubstanceEditService.substance);

    return new Promise((resolve, reject) => {
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.full())
        .andThen(sub => {
          SubstanceEditService.substance = sub;
        })
        .get(sub=>resolve(sub));
    });
  }

  refreshApi() {
    console.log("refresh");
      window["SubstanceFinder"].get(SubstanceEditService.uuid)
        .andThen(s=>s.full())
        .andThen(sub => {
          this.setFullJson(sub);
        }).get(()=>{});
  }

  getFullJson(): Promise<any> {
    return this._getFullJson()
               .then(s=>{
                 if(SubstanceEditService.names)s["names"]=SubstanceEditService.names;
                 if(SubstanceEditService.refs)s["references"]=SubstanceEditService.refs;
                 if(SubstanceEditService.codes)s["codes"]=SubstanceEditService.codes;
                 if(SubstanceEditService.relations)s["relationships"]=SubstanceEditService.relations;
                 return s;
               });
  }





  storeCodes(codes: any){
    let cds= _.map(codes, n=> {
      return (new Code().merge(n));
    });

    if(!SubstanceEditService.codes){
      SubstanceEditService.codes=cds;
    }else{
      console.log("length");
      console.log(SubstanceEditService.codes.length);
      //SubstanceEditService.codes.slice(-SubstanceEditService.codes.length);
      SubstanceEditService.codes.length = 0;

      _.forEach(cds, function(c){
        SubstanceEditService.codes.push(c);
      });
    }
    return SubstanceEditService.codes;
  }

  storeRelationships(relations: any){
    let rels= _.map(relations, r=> {
      return (new Relationship().merge(r));
    });

    if(!SubstanceEditService.relations){
      SubstanceEditService.relations=rels;
    }else{
      SubstanceEditService.relations.length =0;

      _.forEach(rels, function(n){
        SubstanceEditService.relations.push(n);
      });
    }
    return SubstanceEditService.relations;
  }

  storeNames(names: any){
    let mnames= _.map(names, n=> {
      return (new Name().merge(n));
    });

    if(!SubstanceEditService.names){
      SubstanceEditService.names=mnames;
    }else{
      //SubstanceEditService.names.slice(-SubstanceEditService.names.length);
      SubstanceEditService.names.length =0;

      _.forEach(mnames, function(n){
        SubstanceEditService.names.push(n);
      });
    }
    return SubstanceEditService.names;
  }

  storeReferences(refs: any){
    var i=1;
      var mrefs=_.chain(refs)
        .map(r=>new Reference().merge(r))
        .map(r=>{r.index=i++;return r;})
        .value();


    SubstanceEditService.refs = mrefs;
    return mrefs;
  }


  setFullJson(substance: any){
    SubstanceEditService.substance=substance;
    console.log("version");
    console.log(substance.version);

    this.storeCodes(substance["codes"]);
    this.storeNames(substance["names"]);
    this.storeReferences(substance["references"]);
  }


  deleteName(name: Name): Name[]{
    console.log("delete name service");
    var loc = SubstanceEditService.names.indexOf(name);
    if(loc>-1){
      SubstanceEditService.names.splice(loc,1);
    }
    return SubstanceEditService.names;
  }

  deleteCode(code: Code): Code[]{
    console.log("delete code service");
    var loc = SubstanceEditService.codes.indexOf(code);
    if(loc>-1){
      SubstanceEditService.codes.splice(loc,1);
    }
    return SubstanceEditService.codes;
  }

  deleteRelationship(relation: Relationship): Relationship[]{
    console.log("delete relation service");
    var loc =SubstanceEditService.relations.indexOf(relation);
    if(loc>-1){
      SubstanceEditService.relations.splice(loc,1);
    }
    return SubstanceEditService.relations;
  }

  deleteReference(ref: Reference): Reference[]{
    console.log("delete ref service");
    var loc = SubstanceEditService.refs.indexOf(ref);
    if(loc>-1){
      SubstanceEditService.refs.splice(loc,1);
    }
    return SubstanceEditService.refs;
  }
}



