import {Injectable} from "@angular/core";
import {ReferencedData} from "../references/referencedData";
import {Reference} from "../references/reference";
import {Amount} from "../amount/amount.model";
import * as _ from "lodash";


@Injectable()
export class UtilService {

  references: Reference[] = [];

  public appContxt: string  = "/app";
  public browserurl: string = window.location.href.split(this.appContxt)[0];
 // public browserurl: string ="http://localhost:9000";

  getBrowserUrl(): string{
    return this.browserurl;
  }

  changeAccess($event, mod:any): void {
    console.log(mod);
    console.log($event);
    if($event.value.length == 0){
      mod.access = [];
    } else if($event.value.length > 1){
      let index: number = $event.value.indexOf("public");
      if(index != -1){
        mod.access.splice(index, 1);
      }
    }
  }

  showReferenceIndexes(data: ReferencedData){
    if(data.references) {
      return _.chain(this.references)
        .filter(r=>(data.references.indexOf(r.uuid) >= 0))
        .map("index")
        .value();
    } else {
      return "";
    }
  }

  displayAmount(amt: Amount): string {

    let ret = "";

    if(amt) {

      if (amt.type) {
        ret += amt.type + " \n ";
      }

      if (amt.average || amt.low || amt.high) {

        if (amt.average) {
          ret += amt.average + " ";
        }

        if (amt.high || amt.low) {

          ret += " [";

          if (amt.high && !amt.low) {
            ret += "<" + amt.high;
          } else if (!amt.high && amt.low) {
            ret += ">" + amt.low;
          } else if (amt.high && amt.low) {
            ret += amt.low + " to " + amt.high;
          }
          ret += "] (average)";

        }

        if (amt.highLimit || amt.lowLimit) {
          ret += "\n[";
        }
        if (amt.highLimit && !amt.lowLimit) {
          ret += "<" + amt.highLimit;
        } else if (!amt.highLimit && amt.lowLimit) {
          ret += ">" + amt.lowLimit;
        } else if (amt.highLimit && amt.lowLimit) {
          ret += amt.lowLimit + " to " + amt.highLimit;
        }
        if (amt.highLimit || amt.lowLimit) {
          ret += "] (limits)";
        }

        if (amt.units) {
          ret += amt.units;
        }

        if (amt.nonNumericValue) {
          ret += "\n" + amt.nonNumericValue;
        }

      }
    }
    return ret;
  }
}





