import {Component, Input, OnInit, ViewChild} from "@angular/core";
import {CVService} from "../cv/cv.service";
import {PropertyService} from "./property.service";
import {Property} from "./property.model";
import {UtilService} from "../utils/utils.service";
import {Parameter} from "../parameters/parameter";
import {Amount} from "../amount/amount.model";


@Component({
  selector: 'property-edit',
  templateUrl: './property-edit.html',
  providers: [CVService, PropertyService]

})
export class PropertyEditComponent implements OnInit {

  @Input()
  property:Property;

  propName:any[] = [];
  propType:any[] = [];
  access:any[] = [];
  amountDisplay:boolean = false;
  parameters: Parameter[] = [];

  constructor(private cvService:CVService,
              private propertyService:PropertyService,
              private utilService: UtilService) {
  }


  getAccess(): void {
    this.cvService
     .getList("ACCESS_GROUP")
    //  .getAccess()
      .then(access => this.access = access);
  }

  getPropertyName(): void {
    this.cvService
      .getList("PROPERTY_NAME")
      .then(pn => this.propName = pn);
  }

  getPropertyType(): void {
    this.cvService
      .getList("PROPERTY_TYPE")
      .then(pt => this.propType = pt);
  }



  ngOnInit(): void {
    this.getAccess();
    this.getPropertyName();
    this.getPropertyType();
  }

  saveProperty(property: any): void {
    console.log(property);
    console.log("save");
    this.propertyService.saveProperty(property);
  }

  deleteProperty(property: any): void {
    console.log("delete");
    this.propertyService.deleteProperty(property);
  }

  changeAccess($event, property: Property): void {
    this.utilService.changeAccess($event, property);
  }

  public showAmounts(amount:Amount): string {
    return this.utilService.displayAmount(amount);
  }
}



