import {Component, OnInit} from "@angular/core";
import {Reference} from "../references/reference";
import {ConfirmationService} from 'primeng/primeng';
import {ReferenceListService} from '../references/reference-list.service';
import {CVService} from '../cv/cv.service';
import {Code} from './code';
import {UtilService} from "../utils/utils.service"
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {ReferencedData} from "../references/referencedData";
import {ReferenceListDialog} from "../references/reference-list-dialog.component";

import {MdDialog, MdDialogConfig} from "@angular/material";
import * as _ from "lodash";
import {term} from "../cv/cv";
@Component({
  selector: 'code-list',
  templateUrl: './code-list.html',
  //styleUrls: ['../styles.css']
  providers: [ReferenceListService, ConfirmationService, CVService, UtilService, SubstanceEditService]

})
export class CodeListComponent implements OnInit {

  codes: Code[] = [];
  newCode: boolean;
  nCode: Code;
  access: any[] =[];
  codeSystem: any[] = [];
  codeType: any[] = [];
  references: Reference[] = [];

  ngOnInit(): void {
    this.getReferences();
    this.getAccess();
    this.getCodeSystem();
    this.getCodes();
    this.getCodeType();
  }

  constructor(private referenceListService: ReferenceListService,
              private confirmationService: ConfirmationService,
              private cvService: CVService,
              public dialog: MdDialog,
              private utilService: UtilService,
              private substanceEditService: SubstanceEditService) { }

  confirmDeleteCode(code: any) {
    this.confirmationService.confirm({
      message: 'Do you want to delete this Code?',
      header: 'Delete Confirmation',
      icon: 'fa fa-trash',
      accept: () => {
        this.deleteCode(code);
      }
    });
  }

  findSelectedCodeIndex(code:Code): number {
    return this.codes.indexOf(code);
  }

  deleteCode(code: Code){
    this.codes.splice(this.findSelectedCodeIndex(code), 1);
  }

  addCode(top : boolean){
    console.log("add");
    this.newCode = true;
    this.nCode = new Code();
    this.nCode.references = [];
    this.nCode.access = [];
    this.nCode.generateNewUuid();
    if(top){
     this.codes.unshift(this.nCode);
    }else {
      this.codes.push(this.nCode);
    }
  }

  exportRefs(){
    console.log("export codes");
  }

  refresh(){
    this.ngOnInit();
  }

  public showReferenceIndexes(data: ReferencedData): string {
    if(data.references) {
      return _.chain(this.references)
        .filter(r=>(data.references.indexOf(r.uuid) >= 0))
        .map("index")
        .value();
    } else {
      return "";
    }
  }
  search(){
    console.log("search codes");
  }

  getReferences(): void {
    this.substanceEditService
      .getReferences()
      .then(refs =>{
        this.references = refs;
      });
  }

  getCodes(): void {
    this.substanceEditService
      .getCodes()
      .then(codes =>{
        this.codes = codes;
       // this.codes = _.orderBy(this.codes, ['codeSystem'],['asc']);

        this.codes.sort( (a,b)=>{
          return a.codeSystem.localeCompare(b.codeSystem);
        });
      });


  }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);
  }

  getCodeSystem(): void {
    console.log("get code system");
    this.cvService
      .getList("CODE_SYSTEM")
      .then(cs => this.codeSystem = cs);
  }

  getCodeType(): void {
    this.cvService
      .getList("CODE_TYPE")
      .then(cs => this.codeType = cs);
  }

  changeAccess($event, code: Code): void {
    this.utilService.changeAccess($event, code);
  }


  openRefListDialog(name: any) : void {
    console.log("reference list");
    const config = new MdDialogConfig();
    config.width = '400px';
    //config.height = '700px';
    let dialogRef = this.dialog.open(ReferenceListDialog, config);

    //TODO: should have a cleaner initialization process
    dialogRef.componentInstance.allReferences = this.references;
    console.log(name);
    dialogRef.componentInstance.data = name;
    dialogRef.componentInstance.dataList = this.codes;
    dialogRef.componentInstance.closeFunction=function(){
      dialogRef.close();
    };
  }
}



