import {Component, OnInit} from '@angular/core';
import {Relationship} from "./relationship";
import {Reference} from "../references/reference";
import {ReferenceListService} from "../references/reference-list.service";
import {CVService} from "../cv/cv.service";

import {MdDialog, MdDialogConfig} from "@angular/material";
import {ReferenceListDialog} from "../references/reference-list-dialog.component";
import {ReferencedData} from "../references/referencedData";
import {ConfirmationService} from 'primeng/primeng';
import {UtilService} from "../utils/utils.service";
import {Amount} from "../amount/amount.model";

import * as _ from "lodash";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";

@Component({
  selector: 'relationship-list',
  templateUrl: './relationship-list.html',
  providers: [ConfirmationService, SubstanceEditService, UtilService, CVService]

})
export class RelationshipListComponent implements OnInit {

  relations: Relationship[] = [];
  references: Reference[] = [];
  access: any[] = [];
  relationshipTypes: any[] = [];
  filterRelationTypes: any = [];
  selectedFilterType: string = null;

  browserurl: string = this.utilService.getBrowserUrl();

  constructor(private cvService: CVService,
              public dialog: MdDialog,
              private confirmationService: ConfirmationService,
              private utilService: UtilService,
              private substanceEditService: SubstanceEditService) { }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);
  }

  getRelationshipTypes(): void {
    this.cvService
      .getList("RELATIONSHIP_TYPE")
      .then(relTypes => this.relationshipTypes = relTypes);
  }

  ngOnInit(): void {
    this.getReferences();
    this.getAccess();
    this.getRelationshipTypes();
    this.getRelationships();
    this.loadOptions();
    this.selectedFilterType = "";
  }

  getReferences(): void {
    this.substanceEditService
      .getReferences()
      .then(refs =>{
        this.references = refs;
      });
  }

  public showReferenceIndexes(data: ReferencedData): string {
    if(data.references) {
      return _.chain(this.references)
        .filter(r=>(data.references.indexOf(r.uuid) >= 0))
        .map("index")
        .value();
    } else {
      return "";
    }
  }

  openRefListDialog(relation: any) : void {
    console.log("relationship list");
    const config = new MdDialogConfig();
    config.width = '400px';
    let dialogRef = this.dialog.open(ReferenceListDialog, config);


    //TODO: should have a cleaner initialization process
    dialogRef.componentInstance.allReferences = this.references;
    console.log(relation);
    dialogRef.componentInstance.data = relation;
    dialogRef.componentInstance.closeFunction=function(){
      dialogRef.close();
    };
  }

  addRelationship(top: boolean){
    let nRelation: Relationship = new Relationship();
    nRelation = new Relationship();
    nRelation.access = ['protected'];
    nRelation.references = [];
    nRelation.generateNewUuid();
    nRelation.relatedSubstance = null;
    nRelation.type = this.selectedFilterType;
    if(top){
      this.relations.unshift(nRelation);
    }else {
      this.relations.push(nRelation);
    }

    console.log("add new relationship");
  }

  findSelectedRelationIndex(relation: Relationship): number {
    return this.relations.indexOf(relation);
  }

  deleteRelation(relation: Relationship){
    console.log("delete:");
    console.log(relation);
    this.relations.splice(this.findSelectedRelationIndex(relation), 1);
    console.log(this.relations);
  }

  confirmDeleteRelation(relation: Relationship) {
    this.confirmationService.confirm({
      message: 'Do you want to delete this relationship?',
      header: 'Delete Confirmation',
      icon: 'fa fa-trash',
      accept: () => {
        console.log("confirm delete relation");
        this.deleteRelation(relation);
      }
    });
  }

  getRelationships(): void {
    console.log("relationships...");
    this.substanceEditService
      .getRelationships()
      .then(rels => {
        this.relations = rels;
        this.loadOptions();
      //  this.relations = _.orderBy(this.relations, ['relatedSubstance.refPname'],['asc']);
        this.relations.sort( (a,b)=>{
          return a.relatedSubstance.refPname.localeCompare(b.relatedSubstance.refPname);
        });
      });


  }

  changeAccess($event, relation: Relationship): void {
    this.utilService.changeAccess($event, relation);
  }

  public showAmounts(amount:Amount): string {
    return this.utilService.displayAmount(amount);
  }

  loadOptions() : void {
    let result = this.relationshipTypes
                     .filter(o1 => this.relations.some(o2 => o1.value === o2.type))
                     .sort((a,b)=>-a.label.localeCompare(b.label));

    this.filterRelationTypes = result;
    this.filterRelationTypes.push({label:"All Types", value: ""});
    this.filterRelationTypes.reverse();
  }

}


