import {Component, OnInit} from "@angular/core";
import {CVService} from "../cv/cv.service";
import {UtilService} from "../utils/utils.service";
import {ConfirmationService} from "primeng/primeng";
import {Property} from "./property.model";
import {PropertyService} from "./property.service";
import {ReferencedData} from "../references/referencedData";
import {MdDialog} from "@angular/material";
import {ReferenceListDialog} from "../references/reference-list-dialog.component";
import * as _ from "lodash";
import {Amount} from "../amount/amount.model";

@Component({
  selector: 'properties-list',
  templateUrl: './properties-list.html',
  providers: [CVService, ConfirmationService, UtilService, PropertyService]

})
export class PropertiesListComponent implements OnInit {

  propName: any[] = [];
  propType: any[] = [];
  access: any[] = [];
  newProperty: boolean;
  nProperty: Property = new Property(this.propertyService);
  properties: Property[] = [];
  references: any[] = [];
  recordUUID: string = "00006eea-e2d2-4d79-99ff-30f17b3dd740";
  showRSStruct:boolean = false;

  constructor(private cvService: CVService,
              private confirmationService: ConfirmationService,
              private utilService: UtilService,
              private propertyService: PropertyService,
              public dialog: MdDialog){}


  ngOnInit(): void {
    this.getAccess();
    this.getPropertyName();
    this.getPropertyType();
  }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);
  }

  getPropertyName(): void {
    this.cvService
      .getList("PROPERTY_NAME")
      .then(pn => this.propName = pn);
  }

  getPropertyType(): void {
    this.cvService
      .getList("PROPERTY_TYPE")
      .then(pt => this.propType = pt);
  }

  public showReferenceIndexes(data: ReferencedData): string {
    if(data.references) {
      return _.chain(this.references)
        .filter(r=>(data.references.indexOf(r.uuid) >= 0))
        .map("index")
        .value();
    } else {
      return "";
    }
  }

  openRefListDialog(property: any) : void {
    console.log("property list");
    let dialogRef = this.dialog.open(ReferenceListDialog, {height: '550px', width: '400px'});

    //TODO: should have a cleaner initialization process
    dialogRef.componentInstance.allReferences = this.references;
    console.log(property);
    dialogRef.componentInstance.data = property;


    dialogRef.componentInstance.closeFunction=function(){
      dialogRef.close();
    };
  }



  savePropertyListChanges($event, properties : Property[]) {
    console.log("save properties list changes");
    console.log(properties);
  }

  addProperty(){
    this.newProperty = true;
    this.nProperty = new Property(this.propertyService);
    this.nProperty.access = ['protected'];
    this.nProperty.references = [];
    this.properties.push(this.nProperty);
  }

  findSelectedPropertyIndex(property: Property): number {
    return this.properties.indexOf(property);
  }

  deleteProperty(property: Property){
    console.log("delete:");
    console.log(property);
    this.properties.splice(this.findSelectedPropertyIndex(property), 1);
  }

  confirmDeleteProperty(property: Property) {
    this.confirmationService.confirm({
      message: 'Do you want to delete this Property?',
      header: 'Delete Confirmation',
      icon: 'fa fa-trash',
      accept: () => {
        console.log("confirm delete Property");
        this.deleteProperty(property);
      }
    });
  }

  changeAccess($event, property: Property): void {
    this.utilService.changeAccess($event, property);
  }

  showStructure(): void {
    console.log("showStructure");
    this.showRSStruct = !this.showRSStruct;
    console.log(this.showRSStruct);
  }

  public showAmounts(amount:Amount): string {
    return this.utilService.displayAmount(amount);
  }

}



