import * as _ from "lodash";

export class Data<T> {
  uuid:string;
  lastEdited: any;

  merge(json: any): T {
    return _.merge(this, json);
  }
  generateNewUuid(): Data<T> {
    this.uuid = this.uuidHelper().randomUUID();
    console.log("made new uuid:" + this.uuid);

    return this;
  }

  clone(): any{
    return _.cloneDeep(this);
  }

  //processing helpers
  $specialFlags:any={};
  getFlag(key: any): any{
    return this.$specialFlags[key];
  }
  setFlag(key: any, val: any): Data<T>{
    this.$specialFlags[key]=val;
    return this;
  }


  //TODO: move this somewhere more generic
  uuidHelper(): any{
   var UUID = {
  randomUUID: function() {
    return UUID.s4() + UUID.s4() + '-' + UUID.s4() + '-' + UUID.s4() + '-' +
      UUID.s4() + '-' + UUID.s4() + UUID.s4() + UUID.s4();
  },
  s4: function() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  },
  isUUID: function(uuid) {
    if ((uuid + "").match(/^[a-f0-9]{8}-[a-f0-9]{4}-[a-f0-9]{4}-[a-f0-9]{4}-[a-f0-9]{12}$/)) {
      return true;
    }
    return false;
  }
}
return UUID;
 }

}



