import {Component, Input, OnInit} from "@angular/core";
import {CVService} from "../cv/cv.service";
import {Relationship} from "./relationship";
import {UtilService} from "../utils/utils.service";
import {Amount} from "../amount/amount.model";
import {AmountComponent} from "../amount/amount.component";
import {Http, Response} from "@angular/http";
import * as _ from "lodash";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {DataTable} from "primeng/primeng";

@Component({
  selector: 'relation-edit',
  templateUrl: './relationship-edit.html',
  providers: [CVService, SubstanceEditService, UtilService]
})
export class RelationshipEditComponent implements OnInit {

  @Input()
  relationship:Relationship;

  @Input()
    dt: DataTable;

  qualification:any[] = [];
  relationshipTypes:any[] =[];
  access:any[] = [];
  interactionType:any[] = [];
  amountDisplay:boolean = false;
  searchResult: any[] = [];
  isEdit: boolean = true;
  filterSuggestions: any[] = [];
  newAmount: Amount = new Amount();

  searchMResult: any[] =[];
  ismEdit: boolean = true;

  sstate: string = "clean";

  rowCount: number = 2;

  browserurl: string = this.utilService.getBrowserUrl();
  suggesturl: string = this.browserurl + this.utilService.appContxt + "/api/v1/suggest?q=";

  //amount: Amount = new Amount();

  constructor(private cvService:CVService,
              private utilService: UtilService,
              private http: Http,
              private substanceEditService: SubstanceEditService) {
          this.getAccess();
          this.getRelationshipTypes();
          this.getQualification();
          this.getIntercationType();
  }



  closeRow(rel: Relationship, closed: boolean){
    console.log("close row");
    this.dt.toggleRow(rel);
  }


  getAccess(): void {
    this.cvService
      //.getAccess()
      .getList("ACCESS_GROUP")
      .then(access => {
        console.log(access);
        this.access = access;});
  }

  getRelationshipTypes(): void {
    this.cvService
      .getList("RELATIONSHIP_TYPE")
      .then(relTypes => this.relationshipTypes = relTypes);
  }

  getQualification(): void {
    this.cvService
      .getList("QUALIFICATION")
      .then(qual => this.qualification = qual);
  }

  getIntercationType(): void {
    this.cvService
      .getList("INTERACTION_TYPE")
      .then(inTypes => this.interactionType = inTypes);
  }

  ngOnInit(): void {
    console.log(this);
 /*   if(this.relationship && !this.relationship.amount){
      this.relationship.amount = this.amount;
    }*/

  }

  deleteRelationship(relation: any): void {
    console.log("delete relationship");
    this.substanceEditService.deleteRelationship(relation);
  }

  changeAccess($event, relation: Relationship): void {
    console.log($event);
    this.utilService.changeAccess($event, relation);
  }

  public showAmounts(amount:Amount): string {
    return this.utilService.displayAmount(amount);
  }


  changeSelection(){
    this.isEdit=true;
    this.relationship.relatedSubstance = null;
    this.rowCount = 2;
  }

  clearMSelection(){
    this.ismEdit=true;
    this.relationship.mediatorSubstance = null;
    this.rowCount = 2;
  }

  cancelSearch(){
    this.isEdit=false;
    this.searchResult = [];
    this.rowCount = 2;
  }

  cancelMSearch(){
    this.ismEdit=false;
    this.searchMResult = [];
    this.rowCount = 2;
  }

  applySubstance(sr : any){
    console.log(sr);
    this.relationship.relatedSubstance = sr;
    this.searchResult = [];
    this.isEdit = false;
    this.rowCount = 3;
  }

  applyMSubstance(sr : any){
    console.log(sr);
    this.relationship.mediatorSubstance = sr;
    this.searchMResult = [];
    this.ismEdit = false;
    this.rowCount = 3;
    console.log("apply mediator substance");
    console.log(this.relationship);
  }

  getSuggestions(event, isMediator: boolean){
    const endpoint = this.suggesturl + event.query;
    this.http
        .get(endpoint)//, {search: searchParams})
        .subscribe(res =>
        {
          this.filterSuggestions = [];
          if(res.json().Display_Name){
            for(let n of res.json().Display_Name){
              this.filterSuggestions.push(n.key);
            }
          }
          if(res.json().Name){
          for(let n of res.json().Name){
            if(this.filterSuggestions.indexOf(n.key)<=-1) {
              this.filterSuggestions.push(n.key);
            }
          }}
        });
   // this.searchSubstance(event.query, isMediator);
  }

  searchSubstance(query : string, isMediator: boolean){
    this.sstate = "searching";
    console.log("search substance: " + query);

  //TODO: Move to js api
    var toSubRef = function(s){
      var subref: any = {};
      subref.refuuid = s.uuid;
      subref.refPname = s._name;
      subref.approvalID = s.approvalID;
      return subref;
    };


    window["SubstanceFinder"]
      .searchByExactNameOrCode(query)
      .andThen(r=>{
        console.log("Found:" + r.content.total);
        console.log(r);
        return r.content;
      })
      .andThen(c=>{
        return _.map(c, (r)=>toSubRef(r));
      })
      .get(sref=>{
          console.log(sref);
        if(sref.length >  1) {
          this.rowCount = 4;
        }
          if(isMediator){
            this.searchMResult = sref;
            console.log(this.searchMResult.length);
            if (sref.length == 1) {
              this.applyMSubstance(sref[0]);
            }

          } else {
            this.searchResult = sref;
            console.log(this.searchResult.length);
            if (sref.length == 1) {
              this.applySubstance(sref[0]);
            }
          }
      });
      this.sstate = "search-done";
  }


  onSaved(amount: any) {
    this.relationship.amount = amount;
    this.amountDisplay = false;
  }
}



