import {Component} from '@angular/core';
import 'rxjs/add/operator/switchMap';
import {UtilService} from "./utils/utils.service";
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'gsrs-app',
  template: `
<h1 class="title">G-SRS</h1>
<br/>
 <nav>
      <!--<a routerLink="/browse" routerLinkActive="active">Browser</a>
      <a routerLink="/search" routerLinkActive="active">Search</a>
      <a routerLink="/admin" routerLinkActive="active">Admin</a>
      <a routerLink="/login" routerLinkActive="active">Login</a>-->
      <!--<a routerLink="/browse" routerLinkActive="active">Go Back</a>-->

      <a href="{{browserurl}}" target="_blank">Back to GSRS Home</a> &nbsp; &nbsp;
      <a href="{{browserurl}}/substances" target="_blank">Browse Substances</a>
  </nav>
  <br/>
        <router-outlet></router-outlet>
  `,
  providers: [UtilService]
})
export class AppComponent  {

  subId:string;

  constructor(private utilService: UtilService){}

  browserurl: string = this.utilService.getBrowserUrl() + this.utilService.appContxt;


}














