import {Injectable} from "@angular/core";
import {Data} from "../data";
import {Property} from "./property.model";
import * as _ from "lodash";


@Injectable()
export class PropertyService extends Data<PropertyService> {

  properties:Property[] = [];

  saveProperty(property: Property) {
    console.log("property save service");
  }

  deleteProperty(property: Property): Property[]{
    console.log("delete property service");
    var loc = this.properties.indexOf(property);
    if(loc>-1){
      this.properties.splice(loc,1);
    }
    return this.properties;
  }

  addProperty(property: Property): PropertyService {
    if(!property.uuid){
      property.generateNewUuid();
    }
    this.properties.push(property);
    return this;
  }
}



