import {Component, OnInit} from '@angular/core';
import {Name} from "./name";
import {Reference} from "../references/reference";
import {CVService} from "../cv/cv.service";

import {MdDialog, MdDialogConfig} from "@angular/material";
import {ReferenceListDialog} from "../references/reference-list-dialog.component";
import {ReferencedData} from "../references/referencedData";
import {ConfirmationService} from 'primeng/primeng';
import {UtilService} from "../utils/utils.service";
import 'rxjs/add/operator/switchMap';
import { ActivatedRoute, Params } from '@angular/router';


import * as _ from "lodash";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {term} from "../cv/cv";

@Component({
  selector: 'name-list',
  templateUrl: './names-list.html',
  providers: [ConfirmationService, UtilService, SubstanceEditService]
})

export class NameListComponent implements OnInit {

  names: Name[] = [];
  references: Reference[] = [];
  recordUUID: string = "";
  UUID: string;
  nName: Name = new Name();
  tabs: any[] = [];
  nameValidation: any[] = [];

  nameTypes: any[] = [];
  access: term[] = [];
  filteredAccessMultiple: any[] = [];

  getNameTypes(): void {
    this.cvService
      .getList("NAME_TYPE")
      .then(nameTypes => this.nameTypes = nameTypes);
  }

  getAccess(): void {
    console.log("load access - name list");
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);
  }

  constructor(private cvService: CVService,
              public dialog: MdDialog,
              private confirmationService: ConfirmationService,
              private utilService: UtilService,
              private route: ActivatedRoute,
              private substanceEditService: SubstanceEditService) {
  }



  getReferences(): void {
    this.substanceEditService
      .getReferences()
      .then(refs =>{
        this.references = refs;
      });
  }

  getNames(): void {
    this.substanceEditService
      .getNames()
      .then(nams => {
        this.names = nams;

        this.names.sort((a,b)=>{
          if(a.displayName)return -1;
          if(b.displayName)return 1;

          return a.name.localeCompare(b.name);
        })
      });
  }

  getTabs(): void {
    this.cvService.getTabs().then(tabs => {
      this.tabs = tabs;
    });
  }

  public showReferenceIndexes(data: ReferencedData): string {
    if(data.references) {
      return _.chain(this.references)
        .filter(r=>(data.references.indexOf(r.uuid) >= 0))
        .map("index")
        .value();
    } else {
      return "";
    }
  }


/*  refresh(): void{
    this.ngOnInit();
  }*/

  ngOnInit(): void {
    this.recordUUID = this.route.parent.snapshot.params['id'];
    this.getNameTypes();
    this.getAccess();
    this.getTabs();
    this.getNames();
    this.getReferences();

    console.log("Initializing names");
  }

  openRefListDialog(name: any) : void {
    console.log("reference list");
    const config = new MdDialogConfig();
    config.width = '400px';
    //config.height = '700px';
    let dialogRef = this.dialog.open(ReferenceListDialog, config);

    //TODO: should have a cleaner initialization process
    dialogRef.componentInstance.allReferences = this.references;
    console.log(name);
    dialogRef.componentInstance.data = name;
    dialogRef.componentInstance.dataList = this.names;
    dialogRef.componentInstance.closeFunction=function(){
      dialogRef.close();
    };
  }

  addName(top: boolean){
    console.log("add name");
    this.nName = new Name();
    this.nName.languages = ['en'];
    this.nName.access = [];
    this.nName.references = [];
    this.nName.generateNewUuid();
    if(top){
      this.names.unshift(this.nName);
    } else {
      this.names.push(this.nName);
    }
  }

  validateNames(names: Name[]){
    for (let n of names) {
      if(!n.name){
        this.nameValidation.push("Name is a required field");
      }
    }
  }

  findSelectedNameIndex(name:Name): number {
    return this.names.indexOf(name);
  }

  deleteName(name: Name){
    console.log("delete:");
    console.log(name);
    this.names.splice(this.findSelectedNameIndex(name), 1);
  }

  confirmDeleteName(name: Name) {
    this.confirmationService.confirm({
      message: 'Do you want to delete this name?',
      header: 'Delete Confirmation',
      icon: 'fa fa-trash',
      accept: () => {
       console.log("confirm delete name");
        this.deleteName(name);
      }
    });
  }

  changeAccess($event, name: Name): void {
    console.log("access change from name");
    this.utilService.changeAccess($event, name);
  }

  changePreferTerm($event, name){
    for(let n of this.names){
      n.displayName = false;
    }
    name.displayName = true;
  }

  filterAccessMultiple($event, name: Name) {
    let query = $event.query;
    this.cvService.getList("ACCESS_GROUP").then(access => {
      var dif = _.differenceWith(access, name.access, _.isEqual);
      this.filteredAccessMultiple = this.filterAccess(query, access);
    });
  }

  filterAccess(query, access: any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered : any[] = [];
    for(let i = 0; i < access.length; i++) {
      let ac = access[i];
      if(ac.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
        filtered.push(ac);
      }
    }
    return filtered;
  }
}



