import {Data} from "../data";
import {ReferencedData} from "./referencedData";

export class Reference extends Data<Reference>{
  index: number;
  citation: string;
  docType: string;
  url: string;
  access: any[];

  addData(dat: ReferencedData): Reference{
    var dlist=this.getFlag("data");
    if(!dlist){
        dlist=[];
        this.setFlag("data",dlist);
    }
    //TODO: should use UUIDs?
    dlist.push(dat);
    return this;
  }

  removeData(dat: ReferencedData): Reference{
    var dlist=this.getFlag("data");

    if(dlist){
        var idx=dlist.indexOf(dat);
        if(idx>-1){
            dlist.splice(idx,1);
        }
    }
    return this;
  }
  getData(): ReferencedData[]{
    var dlist=this.getFlag("data");
    if(typeof dlist === "undefined"){
        return [];
    }
    return dlist;
  }
  isImported(): Boolean{
    return (typeof this.getFlag("ouuid") !== "undefined");
  }
  isImportedOrphan(): Boolean{
    console.log("imported Orphan");
    return this.isImported() && this.getData().length===0;
  }
}



