import { Component, OnInit } from '@angular/core';
import {CVService} from '../cv/cv.service';
import {SubstanceEditService} from "./substanceedit.service";
import { ActivatedRoute } from '@angular/router';
import {Message} from 'primeng/primeng';
import {Name} from "../names/name";
import * as _ from "lodash";
import {UtilService} from "../utils/utils.service";
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'substanceedit',
  templateUrl: './substanceedit.component.html',
  styleUrls: ['./substanceedit.component.css'],
  providers: [CVService, SubstanceEditService, UtilService]
})
export class SubstanceeditComponent implements OnInit {

  msgs: any[]=[];

  //msgs: Message[][] = [];
  tabs: any[] = [];
  uuid: string;
  message: string = "";
  names: Name[] = [];
  displayName: string = "";
  warningAck: boolean = false;
  state : String = "clean";
  load: string = this.substanceEditService.load;
  browserurl: string = this.utilService.getBrowserUrl();

  constructor(private cvService: CVService,
  private substanceEditService: SubstanceEditService,
  private route: ActivatedRoute,
  private utilService: UtilService,
  private titleService: Title) { }

  getTabs(): void {
    this.cvService.getTabs().then(tabs => {
      this.tabs = tabs;
    });
  }

  tabChange(): void {
    console.log("tab change");
    this.message = "";
    this.msgs = [];
  }

  ngOnInit(): void {
    window["GGlob"].GlobalSettings.setBaseURL(this.browserurl+ this.utilService.appContxt + "/api/v1/");
    this.uuid = this.route.snapshot.params['id'];
    this.getTabs();
    this.substanceEditService.setUuid(this.uuid);
    this.getDisplayName();
  }

  getDisplayName() : string {
    this.substanceEditService
      .getFullJson()
      .then(s => {
       // this.substance = s;
        this.displayName = s._name.toString();
        this.titleService.setTitle(this.displayName? this.displayName : "GSRS");
      });
    return this.displayName;
  }

  save(): void {
    this.state="submitting";
    this.msgs = [];
    this.substanceEditService.getFullJson()
      .then(s=>{
          window["GGlob"]
            .SubstanceBuilder
            .fromSimple(s)
            .save()
            .get(r=>{
              console.log(r);
              let detail = "";

              let messages=_.chain(r.validationMessages)
                .filter(ms=>ms.messageType != "INFO")
                .value();

              r.validationMessages=messages;


              if(r.isError){
                console.log("error");
                this.state="validated-error";
                detail = "Submission Failed:  " ;
                detail += r.type;
                this.showMessage(r.message, r.message, r.type, r.links);
              } else if(r.valid===false) {
                console.log("invalid");
                detail = "Submission Failed:  " ;
                this.state="validated-error";
                for(let m of r.validationMessages) {
                   detail += m.message;
                  this.showMessage(m.messageType, m.messageType, m.message, m.links);
                }
              }else if(r.valid === true && r.validationMessages.length > 0){
                  for(let m of r.validationMessages) {
                      detail += m.message + "\n";
                      this.showMessage(m.messageType, m.messageType, m.message, m.links);
                    }
              } else {
                this.state="submitted";
                console.log("success");
                detail = "Submission Success";
                this.showMessage("success", "",  detail);
                this.substanceEditService.refreshApi();
                /*this.validated = false;
                this.submitted = true;*/
              }
              this.message = detail;
            });

      });
 }

  validate(validation: boolean): void {
    this.state="validating";

    //this.validated = true;
    //this.submitted = false;
    this.msgs = [];
    this.substanceEditService.getFullJson()
      .then(s=>{
        console.log("substance json");
        console.log(s);
        window["GGlob"]
          .SubstanceBuilder
          .fromSimple(s)
          // .save()
          .validate(validation)
          .get(r=>{

            console.log(r);
            let detail = "";

            let messages=_.chain(r.validationMessages)
              .filter(ms=>ms.messageType != "INFO")
              .value();

            r.validationMessages=messages;


            if(r.isError){
              this.state="validated-error";
              console.log("error");
              detail = "Submission Failed: " + r.type;
              this.showMessage(r.message, r.message , r.type, r.links);
            } else if(r.valid===false) {
              this.state="validated-error";
              console.log("invalid");
              detail = "Submission Failed:  " ;
              for(let m of r.validationMessages) {
                detail += m.message;
                this.showMessage(m.messageType, m.messageType, m.message, m.links);
              }
            } else if(r.valid === true) {
              if(r.validationMessages.length > 0){
                for(let m of r.validationMessages) {
                  this.showMessage(m.messageType, m.messageType, m.message, m.links);
                }


                this.state="validated-warning";
                }
               else if(r.validationMessages.length == 0  ) {
                this.save();
              }
            }
            this.message = detail;
          });

      });
  }



  showMessage(sev: string, summary: string,  detail: string, links?: any[]) {
    console.log("show message");
    this.msgs.push({links:links, msgs:[{severity:sev, summary: _.startCase(_.toLower(summary)), detail:detail}]});
  }

  msgclose(msgs: Message[]){
    //if(!this.submitted) {
      this.warningAck = true;
    //}
  }

  closemsg(msg: any){
    console.log("close");
  /*  this.warningAck = true;
    this.msgs = _.chain(this.msgs)
      .filter(m=>m!==msg)
      .value();*/
    this.state = "clean";


  }


}



