import { NgModule }             from '@angular/core';
import { RouterModule, Routes, RouteReuseStrategy } from '@angular/router';
import {NameListComponent} from "./names/name-list.component";
import {CodeListComponent} from "./codes/code-list.component";
import {AppComponent} from "./app.component";
import {RelationshipListComponent} from "./relationships/relationship-list.component";
import {NotesComponent} from "./notes/notes.component";
import {PropertiesListComponent} from "./properties/properties-list.component";
import {SummaryComponent} from "./summaryview/summary.component";
import {ReferenceListComponent} from "./references/reference-list.component";
import {SubstancelistComponent} from "./substancelist/substancelist.component";
import {SubstanceeditComponent} from "./substanceedit/substanceedit.component";
import {CustomRouteReuseStrategy} from "./route-reuse-strategy";


const appRoutes: Routes = [
  //{path: 'substances', component: SubstanceList },
//  { path: 'substance/:id', component: AppComponent},
/*  { path: ':id',
   component: AppComponent },*/

/*  { path: 'test/:id', component: NameListTestComponent,
    resolve: {
      substance: SubstanceDetailResolver
    }},*/

  {
    path: 'substance/:id',
    component: SubstanceeditComponent,
    children: [
      { path: 'names', component: NameListComponent},
      { path: 'references', component: ReferenceListComponent },
      { path: 'codes', component: CodeListComponent },
      { path: 'relationships', component: RelationshipListComponent},
     /* { path: 'notes', component: NotesComponent },
      { path: 'properties', component: PropertiesListComponent },*/
      { path: 'summary', component: SummaryComponent }
    ]
  },

  { path: 'substances', component: SubstancelistComponent },

  { path: '',
    redirectTo: '/substances',
    pathMatch: 'full'
  },

  //{ path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(appRoutes,
    { useHash:true, enableTracing: true }) ],
  exports: [ RouterModule ],
  providers: [
    {
      provide: RouteReuseStrategy,
      useClass: CustomRouteReuseStrategy
    }
  ]
})
export class AppRoutingModule {}



