import {ActivatedRouteSnapshot, DetachedRouteHandle, RouteReuseStrategy, DefaultUrlSerializer} from '@angular/router';

export class CustomRouteReuseStrategy implements RouteReuseStrategy {

  handlers: {[key: string]: DetachedRouteHandle} = {};

  shouldDetach(route: ActivatedRouteSnapshot): boolean {
    console.debug('CustomReuseStrategy:shouldDetach', route);
    return true;
  }

  store(route: ActivatedRouteSnapshot, det: DetachedRouteHandle): void {
    console.debug('CustomReuseStrategy:store', route, det);
    this.handlers[route.routeConfig.path] = det;

    if(det) {
      if (det["componentRef"]._component.detach) {
        det["componentRef"]._component.detach();
      }
    }
  }

  shouldAttach(route: ActivatedRouteSnapshot): boolean {
    console.debug('CustomReuseStrategy:shouldAttach', route);

    if(route.routeConfig.path.indexOf("summary")>=0){
      return !!route.routeConfig && !!this.handlers[route.routeConfig.path];
    }

    return false;
  }

  retrieve(route: ActivatedRouteSnapshot): DetachedRouteHandle {
    console.debug('CustomReuseStrategy:retrieve', route);
    if (!route.routeConfig) return null;
    let det: DetachedRouteHandle = this.handlers[route.routeConfig.path];

    if(det) {
      if (det["componentRef"]._component.refresh) {
        det["componentRef"]._component.refresh();
      }
    }

    return det;
  }

  shouldReuseRoute(future: ActivatedRouteSnapshot, curr: ActivatedRouteSnapshot): boolean {
    console.debug('CustomReuseStrategy:shouldReuseRoute', future, curr);
    return future.routeConfig === curr.routeConfig;
  }

}




