import {Component, Input, OnInit, ViewChild} from "@angular/core";
import {Name} from "./name";
import {CVService} from "../cv/cv.service";
import { trigger, state, style, animate, transition } from '@angular/core';
import {MultiSelectModule, AutoCompleteModule} from 'primeng/primeng';
import {UtilService} from "../utils/utils.service";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {term} from "../cv/cv";
import * as _ from "lodash";

@Component({
  selector: 'name-edit',
  templateUrl: './name-edit.html',
  providers: [CVService, SubstanceEditService]

})
export class NameEditComponent implements OnInit {

  @Input()
  name: Name;

  @Input()
  names: Name[] = [];

  nameTypes: any[] = [];
  access: any[] = [];
  languages: any[] = [];
  nameOrgs: any[] = [];
  nameJuris: any[] = [];
  domains: any[] = [];
  filteredAccessMultiple: any[];

  constructor(private cvService: CVService,
  private utilService: UtilService,
  private substanceEditService: SubstanceEditService) {
    this.getLanguages();
    this.getNameOrgs();
    this.getNameJurisdiction();
    this.getNameTypes();
    this.getAccess();
    this.getDomains();
  }

  getNameTypes(): void {
    this.cvService
      .getList("NAME_TYPE")
      .then(nameTypes => this.nameTypes = nameTypes);
  }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => {
        this.access = access;
      });
  }

  getLanguages(): void {
    this.cvService
      .getList("LANGUAGE")
      .then(lang => this.languages = lang);
  }

  getNameOrgs(): void {
    this.cvService
      .getList("NAME_ORG")
      .then(orgs => this.nameOrgs = orgs);
  }

  getDomains(): void {
    this.cvService
      .getList("NAME_DOMAIN")
      .then(domain => this.domains = domain);
  }

  getNameJurisdiction(): void {
    this.cvService
      .getList("JURISDICTION")
      .then(juris => this.nameJuris = juris);
  }

  ngOnInit(): void { }

  changeDisplay(name: any, $event) : void {
    console.log($event);
    for(let n of this.names){
      n.displayName = false;
    }
    name.displayName = $event.checked;
    //TODO: iterate over all names and make sure atleast one name has displayName flag set to true
  }

/*  setService(nameService: NameService){
    this.nameService=nameService;

  }*/

  deleteName(name: any): void {
    console.log("delete");
    this.substanceEditService.deleteName(name);
  }

  changeAccess($event, name: Name): void {
    this.utilService.changeAccess($event, name);
  }

  filterAccessMultiple($event) {
    let query = $event.query;
    this.cvService.getList("ACCESS_GROUP").then(access => {
      var dif = _.differenceWith(access, this.name.access, _.isEqual);
      this.filteredAccessMultiple = this.filterAccess(query, dif);
    });
  }

  filterAccess(query, access: any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered : any[] = [];

    console.log("filterAccessMultiple");
    console.log(query);
    if(!query){
      console.log("kkk");
      return this.access;
    }


    for(let i = 0; i < access.length; i++) {
      let ac = access[i];
      if(ac.label.toLowerCase().indexOf(query.toLowerCase()) == 0) {
        filtered.push(ac);
      }
    }
    return filtered;
  }

  sayhello($event){
    console.log("hello");
    console.log($event.query);
    if(!$event.query) {
      console.log("suggestionlist");
      this.filteredAccessMultiple = this.access;
      console.log(this.filteredAccessMultiple.length);
    }
  }

}



