import {Component} from "@angular/core";
import {FormControl} from "@angular/forms";
import 'rxjs/add/operator/startWith';
import {Reference} from "./reference";
import {Observable} from "rxjs";
import {ReferenceListService} from "./reference-list.service";
import {ReferenceEditDialog} from "./reference.component";
import {MdDialog} from "@angular/material";
import {ReferencedData} from "./referencedData";
import * as _ from "lodash";
import {Http, Response} from "@angular/http";
import {UtilService} from "../utils/utils.service";


@Component({
  selector: 'reference-list-dialog',
  templateUrl: './reference-list-dialog.html',
  providers: [ReferenceListService, UtilService]

})
export class ReferenceListDialog {

  constructor(private referenceService: ReferenceListService,
              public refDialog: MdDialog,
              private http: Http,
              private utilService: UtilService) {
    this.refCtrl = new FormControl();
    //this.filteredRefs = this.refCtrl.valueChanges
    //  .startWith(null)
    //  .map(citation => citation ? this.filter(citation) : this.references);
  }

  _referenceViewTab: string = "all";
  _filterQuery: string = "";


  closeFunction: any = function(){};          //the specific function to call on close

  refCtrl: FormControl;


  allReferences: Reference[] = [];           //every reference currently present (master list) [full]
  allRefsForData: Reference[] = [];          //those currently displayable
  allRefsForDataFiltered: Reference[] = [];  //those currently displayed



  data: any;                   //the piece of data whose references are being changed
  dataList: ReferencedData[];  //list of names, codes etc. to use it for ApplyAll

  username: string  = "";

  get referenceViewTab(){
    return this._referenceViewTab;
  }

  set referenceViewTab(value){
    if(value!==this._referenceViewTab){
        this._referenceViewTab = value;
        //should trigger view change here
        this.toggleReferences(this._referenceViewTab);
    }

  }

  set filterQuery(q){
    this._filterQuery=q;
    this.filter(this._filterQuery);
  }

  get filterQuery(){
    return this._filterQuery;
  }

  getUsername(){
    const endpoint = this.utilService.browserurl + this.utilService.appContxt + "/api/v1/whoami";
    this.http
      .get(endpoint)
      .subscribe(res =>
      {
        console.log(res.json());
        console.log(res.json().identifier);
        this.username = res.json().identifier;
        console.log(this.username);
      });
  }


  toggleReferences(value: string): any {
    console.log("Clicked:" + value);
    if (value == "all") {
      this.setRefsToAll();
    } else if (value == "last5") {
      console.log(this.username);
      this.setRefsFromAPIMatchingUser(this.username);
    } else if(value == "selected") {
      this.setRefsForData(this.data);
    }
  }

  //Set what references will be displayed
  setDisplayRefs(refs:Reference[]): void {
/*    refs.sort((a,b) => {
      return a.lastEdited - b.lastEdited;
    });*/
     this.allRefsForData=refs;
     this.allRefsForDataFiltered=refs;
  }

  //sets shortlist to be from global thing
  setRefsFromAPIMatchingUser(user): void {
    this.referenceService.getLastFiveReferences(user)
                           .then(refs=>{
                                        _.forEach(refs,r=>r.index=r.index+this.allReferences.length);
                                        this.setDisplayRefs(refs);
                                        });
  }

  //sets the shortlist of checked references to be those
  //found in the given data
  setRefsForData(dat:ReferencedData): void {
      let refs:Reference[] = _.chain(this.allReferences)
        .filter(r=>(dat.references.indexOf(r.uuid) >= 0))
        .value();

    this.setDisplayRefs(refs);
  }

  //sets to all references
  setRefsToAll(): void {
    let refs : Reference[] = this.allReferences;
    this.setDisplayRefs(refs);
  }

  filter(q: string){
    if(q===""){
        this.allRefsForDataFiltered=this.allRefsForData;
    }else{
    this.allRefsForDataFiltered= _.chain(this.allRefsForData)
                                  .filter(r=>(r.citation.indexOf(q)>-1 || (r.index+"")===q))
                                  .value();
    }
  }

  ngOnInit(): void {
    this.toggleReferences(this._referenceViewTab);
    this.getUsername();
  }

  isChecked(ref:Reference) : Boolean {
    let has: Boolean = false;
    if(this.data) {
      has = this.data.hasReference(ref);
    }
    return has;
  }

  applySelectedToAllRecords(): void {

    let refs:Reference[] = _.chain(this.allReferences)
      .filter(r=>(this.data.references.indexOf(r.uuid) >= 0))
      .value();

  console.log(refs.length);
    if(this.dataList) {
      console.log(this.dataList);

      for (let name of this.dataList) {
        if(name.references.length<=0) {
          _.forEach(refs, r => name.addReference(r));
        }
      }
    }
  }

  debug(): void{
    console.log(this.allRefsForData);
  }

  close(): void{
    this.closeFunction();
  }

  // Adds/removes the reference from the list in
  // the data's references
  toggle(toggleReference: Reference){
    console.log("data:");
    console.log(this.data);
    console.log("toggleref");
    console.log(toggleReference);
    let n:ReferencedData = this.data;
    let ref:Reference=toggleReference;

    console.log("looking");
    if(this._referenceViewTab==="last5"){
        var match=_.chain(this.allReferences)
                       .find(r=>r.getFlag("ouuid")===ref.uuid)
                       .value();
        if(!match){
            let r2 : Reference=toggleReference.clone();
            r2.generateNewUuid();
            r2.setFlag("ouuid",toggleReference.uuid);
            r2.index=this.allReferences.length+1;
            this.allReferences.push(r2);
            ref=r2;
        }else{
            ref=match;
        }
    }

if(n) {
  if (n.hasReference(ref)) {
    n.removeReference(ref);
  } else {
    n.addReference(ref);
  }
}

    if(ref.isImportedOrphan()){
        _.remove(this.allReferences, r=>r.uuid===ref.uuid);
    }

    console.log("toggled");
  }


  apply(){
    // do nothing for now, but need this method call
  }



  //filterRefs(val: string) {
  //  return val ? this.references.filter((r) => new RegExp(val, 'gi').test(r.citation)) : this.references;
  //}

  selectedOption: any;
  openRefEditDialog(selectedReference:any) : void {
    //if there is no reference, make one and add it
    if(!selectedReference){
        selectedReference = new Reference();
        selectedReference.generateNewUuid();
        selectedReference.setFlag("new",true);
        selectedReference.index=this.allReferences.length+1;
        selectedReference.lastEdited = new Date();
        this.allReferences.unshift(selectedReference);
    }
    let refEditDialog = this.refDialog.open(ReferenceEditDialog, {height: '700px', width: '550px'});

    refEditDialog.componentInstance.selectedReference = selectedReference;
    refEditDialog.componentInstance.close = function(){refEditDialog.close();};
    refEditDialog.afterClosed().subscribe(result => {
      console.log("Done");
      if(selectedReference.getFlag("delete")){
        _.remove(this.allReferences, r=>r===selectedReference);
      }else{
        selectedReference.setFlag("new",false);
      }
      this.selectedOption = result; //not sure what this does
    });
  }
}



