import {Component, Input, OnInit, ViewChild} from "@angular/core";
import {Code} from "./code";
import {CVService} from "../cv/cv.service";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {UtilService} from "../utils/utils.service";

@Component({
  selector: 'code-edit',
  templateUrl: './code-edit.html',
  providers: [CVService, UtilService, SubstanceEditService]

})
export class CodeEditComponent implements OnInit {

  @Input()
  code: Code;

  codeSystem: any[]= [];
  codeSystemType: any[] = [];
  access: any[] = [];
  codeType: any[] = [];

  constructor(private cvService: CVService,
              private substanceEditService: SubstanceEditService,
              private utilService: UtilService) { }

  getCodeSystemType(): void {
    this.cvService
      .getList("CODE_SYSTEM_TYPE")
      .then(codeSystemType => this.codeSystemType = codeSystemType);
  }

  getCodeSystem(): void {
    this.cvService
      .getList("CODE_SYSTEM")
      .then(cs => this.codeSystem = cs);
  }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);
  }

  getCodeType(): void {
    this.cvService
      .getList("CODE_TYPE")
      .then(type => this.codeType = type);
  }

  ngOnInit(): void {
    this.getCodeSystem();
    this.getAccess();
    this.getCodeType();
    this.getCodeSystemType();
  }

  deleteCode(code: any): void {
    console.log("delete");
    this.substanceEditService.deleteCode(code);
  }

  changeAccess($event, code: Code): void {
    this.utilService.changeAccess($event, code);
  }
}



