import {Component, OnInit, Input} from "@angular/core";
import {Reference} from "./reference";
import {REFERENCES} from "../mock-references";
import {CVService} from "../cv/cv.service";
import {Headers, RequestOptions, Http} from "@angular/http";
import {Observable} from "rxjs/Rx";
import {SubstanceEditService} from "../substanceedit/substanceedit.service";
import {UtilService} from "../utils/utils.service";

@Component({
  selector: 'reference-edit',
  templateUrl: './reference-edit.html',
  //styleUrls: ['../styles.css']
  providers: [CVService, SubstanceEditService, UtilService]

})
export class ReferenceEdit implements OnInit {

  @Input()
  ref: Reference;

  sourceTypeList: any[] = [];
  documentSystemType: any[] = [];
  access: any[] = [];
  tags: any[] = [];
  apiEndPoint: any = this.utilService.browserurl  + this.utilService.appContxt + "/upload";

  close :any = function(){};

  constructor(private cvService: CVService,
  private http: Http,
  private substanceEditService: SubstanceEditService,
  private utilService: UtilService) { }

  updateRef(ref: any, event): void {
    event.preventDefault();

    //TODO:should do something interesting here
    //right now, we are always modifying things, no matter what

    this.close();
  }

  deleteRef(selectedReference: any){
    console.log(selectedReference);
    this.substanceEditService.deleteReference(selectedReference);
    //selectedReference.setFlag("delete",true);
    this.close();
  }

  getSourceType(): void {
    this.cvService
      .getList("DOCUMENT_TYPE")
      .then(st => this.sourceTypeList = st);
  }

  getDocumentSystemType(): void {
    this.cvService
      .getList("DOCUMENT_SYSTEM_TYPE")
      .then(st => this.documentSystemType = st);
  }

  getAccess(): void {
    this.cvService
      .getList("ACCESS_GROUP")
      //.getAccess()
      .then(access => this.access = access);
  }

  getTags(): void {
    this.cvService
      .getList("DOCUMENT_COLLECTION")
      .then(tags => this.tags = tags);

  }

  ngOnInit(): void {
    this.getSourceType();
    this.getDocumentSystemType();
    this.getAccess();
    this.getTags();
  }

  fileUploader($event){
    console.log("file uploader");
    console.log($event);


    let fileList: FileList = $event.target.files;
    if(fileList.length > 0) {
      let file: File = fileList[0];
      console.log(file.name);
      let formData:FormData = new FormData();
      formData.append('uploadFile', file, file.name);
      let headers = new Headers();
      headers.append('Content-Type', 'multipart/form-data');
      headers.append('Accept', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.post(`${this.apiEndPoint}`, formData, options)
        .map(res => res.json())
        .catch(error => Observable.throw(error))
        .subscribe(
          data => console.log('success'),
          error => console.log(error)
        )
    }
  }


  changeAccess($event, ref: Reference): void {
    this.utilService.changeAccess($event, ref);
  }

}



