//need to update the new values in the database for the api call
export const PROPERTY_TYPE: any[] = [
  {value: "PHYSICAL MEASURED", label: "PHYSICAL MEASURED"},
  {value: "PHYSICAL CALCULATED", label: "PHYSICAL CALCULATED"},
  {value: "ENZYMATIC", label: "ENZYMATIC"},
  {value: "CHEMICAL CALCULATED", label: "CHEMICAL CALCULATED"},
  {value: "CHEMICAL MEASURED", label: "CHEMICAL MEASURED"}
];
export const PARAMETER_TYPE: any[] = [
  {value: "PHYSICAL", label: "PHYSICAL"},
  {value: "CHEMICAL", label: "CHEMICAL"},
  {value: "ENZYMATIC", label: "ENZYMATIC"}
];


@Injectable()
export class CVService {

  static cache: any={};



  getList(listName:String): Promise<any[]>{
    return this.getCVLists(listName);
  }

  getParameterType():Promise<any[]>{
    return Promise.resolve(PARAMETER_TYPE);
  }

  getPropertyType():Promise<any[]>{
    return Promise.resolve(PROPERTY_TYPE);
  }


  getTabs():Promise<any[]>{
    return Promise.resolve(COMMON_TABS);
  }

  getCVLists(cvDomain: any): Promise<any []>{
       let c=CVService.cache[cvDomain];

      if(c){
        console.log("from cache " + cvDomain);
        return new Promise((resolve, reject) => {resolve(c);});
      }


      return new Promise((resolve, reject) => {
        console.log("from api" + cvDomain);
        window["GGlob"].CVFinder
          .searchByDomain(cvDomain)
          .andThen(cnt=>cnt.content)
          .andThen(cvs=>{
            var mcvs=_.chain(cvs)
              .flatMap(cv=>cv.terms)
              .map(t=>{
                t.label=t.display;
                return t;
              })
              .sortBy(["label"])
              .uniqBy("value")
              .value();
            CVService.cache[cvDomain]=mcvs;
            return mcvs;
          })
          //TODO: Debugging only!!! But this kind of delay shouldn't
          //stop the program from working
         // .get(cvs=>window["setTimeout"](()=>resolve(cvs),5000));

          //Uncomment for true way
         .get(cvs=>resolve(cvs));
      });

  }
}



