import {Component, OnInit} from "@angular/core";
import {CVService} from '../cv/cv.service';
import {Notes} from './notes';
import {UtilService} from "../utils/utils.service";
import {ConfirmationService} from 'primeng/primeng';

@Component({
  selector: 'notes',
  templateUrl: './notes-list.html',
  providers: [CVService, UtilService, ConfirmationService]
})

export class NotesComponent implements OnInit {

  access: any[] = [];
  newNotes: boolean;
  nNote: Notes;
  recordUUID: string = "00006eea-e2d2-4d79-99ff-30f17b3dd740";
  notes: Notes[] = [];

  constructor(private cvService: CVService,
              private confirmationService: ConfirmationService,
              private utilService: UtilService){}


  ngOnInit(): void {
    this.getAccess();
  }

  getAccess(): void {
    this.cvService
      //.getAccess()
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);

 /*   this.cvService
      .getList("ACCESS_GROUP")
      .then(access => this.access = access);*/
  }

  saveNoteListChanges($event, notes : Notes[]){
    console.log("save notes list changes");
    console.log(notes);
  }

  changeAccess($event, notes: Notes): void {
    this.utilService.changeAccess($event, notes);
  }

  addNotes(){
    console.log("Notes");
    this.newNotes = true;
    this.nNote = new Notes();
    this.nNote.access = ['protected'];
    this.notes.push(this.nNote);
  }

  confirmDeleteNotes(notes: any) {
    this.confirmationService.confirm({
      message: 'Do you want to delete this notes?',
      header: 'Delete Confirmation',
      icon: 'fa fa-trash',
      accept: () => {
        this.deleteNotes(notes);
      }
    });
  }

  findSelectedNotesIndex(notes: Notes): number {
    return this.notes.indexOf(notes);
  }

  deleteNotes(notes: Notes){
    console.log("delete:");
    console.log(notes);
    this.notes.splice(this.findSelectedNotesIndex(notes), 1);
  }
}



