import {Injectable} from "@angular/core";
import * as _ from "lodash";
import {Data} from "../data";
import {Notes} from "./notes";

@Injectable()
export class NoteService extends Data<NoteService> {

  noteList: Notes[] = [];

   saveNote(note: Notes) {
    console.log("note save service");
  }

  deleteNote(note: Notes): Notes[]{
    console.log("delete Notes service");
    var loc = this.noteList.indexOf(note);
    if(loc>-1){
      this.noteList.splice(loc,1);
    }
    return this.noteList;
  }

  addNote(note: Notes): NoteService {
    if(!note.uuid){
      note.generateNewUuid();
    }
    this.noteList.push(note);
    return this;
  }

}



